import React from "react";
import ReactDOM from "react-dom";
import bridge from "@vkontakte/vk-bridge";
import { View, Panel, PanelHeader, Div, Button, PanelHeaderBack} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

bridge.send("VKWebAppInit");

class App extends React.Component {
  constructor(props) {
  super(props);
  this.state = {
      activePanel: 'main'
    }}
  render() {
  return (
    <View activePanel={this.state.activePanel}>

      <Panel id="main">
        <PanelHeader>{"Меню"}</PanelHeader>

               <Div>
               <Button stretched onClick={ () => this.setState({ activePanel: 'test' }) }>Тест</Button>
               </Div>

      </Panel>

      <Panel id="test">
        <PanelHeader left={<PanelHeaderBack  onClick={ () => this.setState({ activePanel: 'main' }) } />}>
        Тест
        </PanelHeader>


      </Panel>

    </View>
  );
}

}

ReactDOM.render(<App />, document.getElementById("root"));
